import React from "react";
import { useEffect, useState } from "react";

export default function Guestform({props , updateGuest}) {
    
    const [edit, setEdit] = useState(props.rsvpSubmitted)
    const [coming, setComing] = useState(props.coming);
    const [dietRes, setDietRes] = useState(props.dietaryRes)
    const [mainCourse, setMainCourse] = useState(props.dinnerChoice);
    const [sleepover, setSleepover] = useState(props.sleepOver);
    const [rickBus, setRickBus] = useState(props.busRide);
    const [songRequestTitle, setSongRequestTitle] = useState(props.songReqTitle);
    const [songRequestArtist, setSongRequestArtist] = useState(props.songReqArtist);
    const [guestComment, setGuestComment] = useState(props.comments)

    useEffect(() => {
        setEdit(!props.rsvpSubmitted)
    },[props.lastModified])

    function handleGuestSubmit() {
        // e.preventDefault();
        console.log(props)
        let guestVals = props;

        guestVals.coming = coming;
        guestVals.dietaryRes = dietRes;
        guestVals.dinnerChoice = mainCourse;
        guestVals.sleepOver = sleepover;
        guestVals.busRide = rickBus;
        guestVals.songReqTitle = songRequestTitle;
        guestVals.songReqArtist = songRequestArtist;
        guestVals.comments = guestComment;
        
        updateGuest(guestVals,props.guestID)
        // setEdit(false)
    }

    const showOptions = (

            <div className="grid gap-3 grid-flow-row grid-cols-1 sm:grid-cols-2 mx-3 my-3">  
            {/* Dinner Options */}
            <h4 className="col-span-full text-center p-2">Dinner Options</h4>
            <div className={"flex flex-col  " + (mainCourse === "Pork" ? "drop-shadow-lg" : "opacity-70")}>
                <button className={"py-2 mx-auto my-3 w-[60%] border-2 text-lg rounded-xl ease-linear transition-all duration-150 shadow shadow-yellow-100 hover:shadow-lg hover:shadow-yellow-100 active:bg-sky-600 focus:border-rose-100 border-slate-600/70 " +
                    (mainCourse === "Pork" ? "bg-sky-300 text-slate-600" : "bg-sky-200 text-slate-400")}
                    onClick={() => setMainCourse("Pork")}
                    type="button">
                    PORK 
                </button>
                <p className="bg-slate-200 rounded-3 p-2 mx-3">
                    The pork is a fine roast with many herbs and spices. Served with cherry potatoes and a russet salad
                </p>
            </div>
            <div className={"flex flex-col " + (mainCourse === "Chicken" ? "drop-shadow-lg" : "opacity-70")}>
                <button className={"py-2 mx-auto my-3 w-[60%] border-2 text-lg rounded-xl ease-linear transition-all duration-150 shadow shadow-yellow-100 hover:shadow-lg hover:shadow-yellow-100 active:bg-sky-600 focus:border-rose-100 border-slate-600/70 " +
                    (mainCourse === "Chicken" ? "bg-sky-300 text-slate-600" : "bg-sky-200 text-slate-400")}
                    onClick={() => setMainCourse("Chicken")}
                    type="button">
                    CHICKEN
                </button>
                <p className="bg-slate-200 rounded-3 p-2 mx-3">
                    Stokely is proud to provide their finest boiled chicken breast with an optional side of salt ($3.00 extra)
                </p>
            </div>
            <div className={"flex flex-col " + (mainCourse === "Veggie" ? "drop-shadow-lg" : "opacity-70")}>
                <button className={"py-2 mx-auto my-3 w-[60%] border-2 text-lg rounded-xl ease-linear transition-all duration-150 shadow shadow-yellow-100 hover:shadow-lg hover:shadow-yellow-100 active:bg-sky-600 focus:border-rose-100 border-slate-600/70 " +
                    (mainCourse === "Veggie" ? "bg-sky-300 text-slate-600" : "bg-sky-200 text-slate-400")}
                    onClick={() => setMainCourse("Veggie")}
                    type="button">
                    VEGETARIAN 
                </button>
                <p className="bg-slate-200 rounded-3 p-2 mx-3">
                    Milk soup with some eggy stuff. No gabagoooooo
                </p>
            </div>
            <div className={"flex flex-col " + (mainCourse === "Vegan" ? "drop-shadow-lg" : "opacity-70")}>
                <button className={"py-2 mx-auto my-3 w-[60%] border-2 text-lg rounded-xl ease-linear transition-all duration-150 shadow shadow-yellow-100 hover:shadow-lg hover:shadow-yellow-100 active:bg-sky-600 focus:border-rose-100 border-slate-600/70 " +
                    (mainCourse === "Vegan" ? "bg-sky-300 text-slate-600" : "bg-sky-200 text-slate-400")}
                    onClick={() => setMainCourse("Vegan")}
                    type="button">
                    VEGAN
                </button>
                <p className="bg-slate-200 rounded-3 p-2 mx-3">
                    Leafs
                </p>
            </div>
            <div className="col-span-full flex flex-col place-items-stretch ">
                <label htmlFor="alergies" className="mt-2 mb-1 mx-[6%] text-sm font-medium text-slate-700">Any neccessary dietary restrictions? Let us know!</label>
                <input 
                    type="text" 
                    id="alergies"
                    value={dietRes}
                    placeholder="none" 
                    onChange={(e) => setDietRes(e.target.value)} 
                    className="mb-3 mx-[5%] pl-3 py-1 rounded-full bg-black text-white">
                </input>
            </div>

            {/* Staying the night and bus?? */}
            <div className="flex flex-col place-items-stretch" >
                < p className="bg-slate-200 rounded-3 p-2 mx-3 my-3 grow">
                    Are you interested in spending the night at Stokely? There are a number of beds available. Details will be available closer to the date.
                </p>
                <div className="flex flex-row flex-wrap mb-3 place-items-stretch">
                    <button className={"transition ease-in-out delay-75 sm:hover:scale-105 mx-3 my-1 py-1 grow min-w-[100px] rounded-2 text-lg " + (sleepover ? "bg-slate-600 text-slate-200" : "bg-slate-200 text-slate-700")}
                        onClick={() => setSleepover(true)}
                        type="button">
                        YES
                    </button>
                    <button className={"transition ease-in-out delay-75 sm:hover:scale-105 mx-3 my-1 py-1 grow min-w-[100px] rounded-2 text-lg " + (sleepover ? "bg-slate-200 text-slate-700" : "bg-slate-600 text-slate-200")}
                        onClick={() => setSleepover(false)}
                        type="button">
                        NO
                    </button>
                </div>
                
            </div>
            <div className="flex flex-col" >
                <p className="bg-slate-200 rounded-3 p-2 mx-3 my-3">
                    We are looking to book transportation back to town. Would you be interested in a ride? We will set a couple drop off locations around town depending who is on the bus.
                </p>
                <div className="flex flex-row flex-wrap mb-3 place-items-stretch">
                    <button className={"transition ease-in-out delay-75 sm:hover:scale-105 mx-3 my-1 py-1 grow min-w-[100px] rounded-2 text-lg " + (rickBus ? "bg-slate-600 text-slate-200" : "bg-slate-200 text-slate-700")}
                        onClick={() => setRickBus(true)}
                        type="button">
                        YES
                    </button>
                    <button className={"transition ease-in-out delay-75 sm:hover:scale-105 mx-3 my-1 py-1 grow min-w-[100px] rounded-2 text-lg " + (rickBus ? "bg-slate-200 text-slate-700" : "bg-slate-600 text-slate-200")}
                        onClick={() => setRickBus(false)}
                        type="button">
                        NO
                    </button>
                </div>
                
            </div>

            {/* song request */}
            <div className="flex flex-col  drop-shadow-lg">
                <h4 className="py-2 px-3 mx-3 mt-3 bg-red-200 text-blue-900 text-center rounded-2">Song Request</h4>
                <label htmlFor="songReqName" className="mb-1 mx-[11%] text-sm font-medium text-slate-700">Song Title</label>
                <input 
                    type="text" 
                    id="songReqName"
                    value={songRequestTitle}
                    placeholder="Song Name" 
                    onChange={(e) => setSongRequestTitle(e.target.value)} 
                    className="mb-1 mx-[9%] pl-3 py-1 rounded-full bg-black text-white">
                </input>
                <label htmlFor="songReqArtist" className=" mb-1 mx-[11%] text-sm font-medium text-slate-700">Artist</label>
                <input 
                    type="text" 
                    id="songReqArtist"
                    value={songRequestArtist}
                    placeholder="none" 
                    onChange={(e) => setSongRequestArtist(e.target.value)} 
                    className="mb-3 mx-[9%] pl-3 py-1 rounded-full bg-black text-white">
                </input>
            </div>
            <div className="flex flex-col place-items-stretch ">
                <h4 className="py-2 px-3 mx-3 my-3 bg-red-200 text-blue-900 text-center rounded-2">
                    Comments or Questions? 
                </h4>
                <textarea 
                    id="comments"
                    value={guestComment}
                    onChange={(e) => setGuestComment(e.target.value)}
                    className="px-3 py-2 mx-3 mb-3 mt-1 min-h-[100px] rounded-3 grow bg-black text-white" >

                </textarea>
            </div>

            {/* mother's mating name */}
            </div>
        )
        // onSubmit={() => handleGuestSubmit()}
    const showGuestForm = (

        <div  className="">
            <div className="col-span-full flex flex-row flex-wrap">
                <button className={"basis-5/12 py-2 mx-auto my-3 ease-linear transition-all duration-150 shadow shadow-yellow-100 hover:shadow-lg hover:shadow-yellow-100 active:bg-sky-600 focus:border-rose-100 rounded-xl border-2 " + (!coming ? "bg-sky-300 text-slate-600  border-slate-600/70 " : "bg-sky-200 text-slate-400  border-slate-600/70")}
                    onClick={() => setComing(false)}
                    type="button">
                    Not Interested
                </button>
                <button className={"basis-5/12 py-2 mx-auto my-3 ease-linear transition-all duration-150 shadow shadow-yellow-100 hover:shadow-lg hover:shadow-yellow-100 active:bg-sky-600 focus:border-rose-100 rounded-xl border-2 " + (coming ? "bg-sky-300 text-slate-600  border-slate-600/70 " : "bg-sky-200 text-slate-400  border-slate-600/70")}
                    onClick={() => setComing(true)}
                    type="button">
                    I'm Coming!
                </button>
            </div>
            
            {coming ? showOptions : null}

            <button 
                className={"transition ease-in-out delay-75 sm:hover:-translate-y-0.5 basis-full text-xl py-1 px-3 mb-3 mx-3 rounded-2 border-2 border-white bg-slate-200 text-slate-700"} 
                onClick={() => handleGuestSubmit()}>
                SUBMIT
            </button>
        </div>
        
    )

    const showDetails = (

        <>
            <div className="grid grid-flow-row grid-cols-2 md:grid-cols-4 rounded-xl px-1 py-3 text-justify-center">
                <p className="bg-white/70 shadow-sm shadow-slate-700 text-slate-600 text-right pr-3 mt-2 mb-0 ml-3 mr-1 rounded">Dinner Selection:</p>
                <p className="bg-white/70 shadow-sm shadow-slate-700 text-slate-600 text-left pl-3 mt-2 mb-0 mr-3 ml-1 rounded">{props.dinnerChoice}</p>
                <p className="bg-white/70 shadow-sm shadow-slate-700 text-slate-600 text-right pr-3 mt-2 mb-0 ml-3 mr-1 rounded">Dietary Restrictions:</p>
                <p className="bg-white/70 shadow-sm shadow-slate-700 text-slate-600 text-left pl-3 mt-2 mb-0 mr-3 ml-1 rounded">{props.dietaryRes}</p>
                <p className="bg-white/70 shadow-sm shadow-slate-700 text-slate-600 text-right pr-3 mt-2 mb-0 ml-3 mr-1 rounded">Spending the night:</p>
                <p className="bg-white/70 shadow-sm shadow-slate-700 text-slate-600 text-left pl-3 mt-2 mb-0 mr-3 ml-1 rounded">{props.sleepOver ? "Yes" : "No"}</p>
                <p className="bg-white/70 shadow-sm shadow-slate-700 text-slate-600 text-right pr-3 mt-2 mb-0 ml-3 mr-1 rounded">Taking the bus:</p>
                <p className="bg-white/70 shadow-sm shadow-slate-700 text-slate-600 text-left pl-3 mt-2 mb-0 mr-3 ml-1 rounded">{props.busRide ? "Yes" : "No"}</p>
                <p className="bg-white/70 shadow-sm shadow-slate-700 text-slate-600 text-right pr-3 mt-2 mb-0 ml-3 mr-1 rounded">Song Request Title:</p>
                <p className="bg-white/70 shadow-sm shadow-slate-700 text-slate-600 text-left pl-3 mt-2 mb-0 mr-3 ml-1 rounded">{props.songReqTitle}</p>
                <p className="bg-white/70 shadow-sm shadow-slate-700 text-slate-600 text-right pr-3 mt-2 mb-0 ml-3 mr-1 rounded">Song Request Artist:</p>
                <p className="bg-white/70 shadow-sm shadow-slate-700 text-slate-600 text-left pl-3 mt-2 mb-0 mr-3 ml-1 rounded">{props.songReqArtist}</p>
                <p className="bg-white/70 shadow-sm shadow-slate-700 text-slate-600 col-span-full md:col-span-1 md:text-right pr-3 mt-2 mb-0 ml-3 mr-1 rounded">Comments:</p>
                <p className="bg-white/70 shadow-sm shadow-slate-700 text-slate-600 col-span-full md:col-span-3 md:text-left pl-3 mt-2 mb-0 mr-3 ml-1 rounded">{props.comments}</p>
                <button
                onClick={() => setEdit(true)} 
                className={"self-center bg-sky-300 text-slate-600 border-2 py-1 px-6 my-4 ml-3 border-slate-600/70 active:bg-sky-600 focus:border-2 focus:border-rose-100 font-bold uppercase text-base rounded-xl shadow shadow-yellow-100 hover:shadow-lg " +
                    " hover:shadow-yellow-100 outline-none focus:outline-none ease-linear transition-all duration-150 "}
                    >
                EDIT
            </button>
            </div>
            
        </>
        )

    return(
        <div className="my-6 mx-6 p-3 border-t-8 border-b-8 border-double border-slate-600/70 rounded-2xl bg-white/80">
            <h3 className="text-slate-600 text-2xl text-center font-semibold w-[75%] py-1 px-3 mb-5 mx-auto border-b-2 border-slate-600/70">
                {props.guestName}
            </h3>
            <div>
                {(props.rsvpSubmitted && !edit) ? showDetails : showGuestForm}
            </div>
           
        </div>
    )

}







